* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
html {
    font-size: 62.4%;
}
body {
    background: url("../assets/img/fond.jpg") no-repeat fixed center center / cover;
    font-size: 1.6rem;
    min-height: 100vh;
}
h1 {
    text-align: center;
    font-family: "MV Boli", Sans-Serif;
    border: black 4px solid;
    width: 200px;
    margin: 20px auto;
}
.keybord {
    display: -ms-flexbox;
    display: flex;
    width: 50%;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    margin: 0 auto;
}
.keybord p {
    margin: 1px;
    border: solid 2px black;
    padding: 2% 4%;
    background-color: grey;
    cursor: pointer;
    color: white;
}
.try {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.try p {
    padding: 5px;
}
.try p, .flex p {
    text-shadow: 2px 2px 4px #000000;
    font-size: 1.5em;
}
.tools {
    position: relative;
}
.canvas {
    position: relative;
    text-align: center;
    width: 500px;
    margin: 0 auto;
}
.otherLetters {
    position: absolute;
    top: 100px;
    right: 0;
    width: 20%;
}
@media (max-width: 850px) {
    .otherLetters {
        position: static;
        margin: 0 auto;
        padding: 30px 20px;
        width: 100%;
        text-align: center;
    }
}
.flex {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}
.flex p {
    padding: 10px;
}
.start {
    position: absolute;
    top: 10%;
    right: 10%;
}
.start button {
    padding: 10px 30px;
    background-color: #00344a;
    color: white;
}
#pendu {
    background-color: rgb(41, 41, 41);
    border-radius: 20px;
}
.clearfix::after {
    content: ' ';
    clear: both;
    display: block;
    line-height: 0;
    font-size: 0;
}



